import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';
import twitter from 'twitter-text';

const image = document.getElementById('image');
const cropper = new Cropper(image, {
  viewMode: 1,
  dragMode: 'crop',
  initialAspectRatio: 16 / 9,
  crop(event) { updateFormValues(event) },
})

function updateFormValues(event) {
  document.getElementById('cropLeft').value = event.detail.x;
  document.getElementById('cropTop').value = event.detail.y;
  document.getElementById('cropWidth').value = event.detail.width;
  document.getElementById('cropHeight').value = event.detail.height;
}

const characterCount = document.getElementById('imageCaption');
const characterDisplay = document.getElementById('captionCharacterCount');
characterCount.oninput = event => {
  const entered_text = event.srcElement.value;
  const result = twitter.parseTweet(entered_text);
  captionCharacterCount.innerText = result.weightedLength + " / 280";
}
